import Vue from 'vue'
import router from './router/router'
import App from './App'
import './mock/mockServer'
import './lib/hotcss/src/hotcss'
import './common/scss/fonts.scss'

Vue.config.productionTip = false


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



